import React, {useEffect, useState} from 'react';
import {Button, Grid, Rating, Stack, Typography, TextField, InputAdornment} from '@mui/material';
import DataTable from 'components/@extended/DataTable';
import {useNavigate} from 'react-router-dom';
import {getPostList} from 'api/board';
import LocalAtmIcon from '@mui/icons-material/LocalAtm';
import RechargeDialog from 'components/RechargeDialog';
import ExchangeDialog from 'components/ExchangeDialog';

const Mileage = () => {
	// [이벤트 매핑] useNavigate에서 navigate 가져오기
	const navigate = useNavigate();

	//[api 연결] data(기본값 : []), isLoading(기본값 : false)을 컴포넌트 State로 선언(useState)
	const [data, setData] = useState([]);
	const [isLoading, setLoading] = useState(false);

	const [rechargeOpen, setRechargeOpen] = React.useState(false);
	const [selectedValue, setSelectedValue] = React.useState(null);

	const [exchangeOpen, setExchangeOpen] = React.useState(false);

	const rechargeClickOpen = () => {
		setRechargeOpen(true);
	};

	const rechargeClose = (value) => {
		setRechargeOpen(false);
		setSelectedValue(value);
	};

	const exchangeClickOpen = () => {
		setExchangeOpen(true);
	};

	const exchangeClose = (value) => {
		setExchangeOpen(false);
		setSelectedValue(value);
	};
	//
	useEffect(() => {
		// [api 연결] 로딩 시 게시글 리스트 api 호출 함수 실행
		findPostList();
	}, []);

	const findPostList = async () => {
		// [api 연결] isLoading의 상태값 변경(로딩중이면 true, 아니면 false)
		setLoading(true);

		// [api 연결] Todo: api/board.js에서 게시글 리스트 api 호출 후 결과 받기
		//const response = await getPostList();

		// [api 연결] Todo: data에 결과 매핑하기
		//setData(response);
		//Sample Data
		setData([
			{date: '2023-07-08', type: '충전', change: '+10,000', remain: '10,000'},
			{date: '2023-07-08', type: '사용(내 아이 돌봄)', change: '-3,000', remain: '7,000'},
			{date: '2023-07-08', type: '적립(이웃 아이 돌봄)', change: '+5,000', remain: '12,000'},
			{date: '2023-07-09', type: '환전', change: '-2,000', remain: '10,000'},
		]);
		setLoading(false);
	};

	const moveCreatePostPage = () => {
		// [이벤트 매핑] Todo: 글쓰기 페이지로 이동(/post/write)
		navigate('/my-icare/write');
	};

	const rowClick = (e, row) => {
		// [이벤트 매핑] Todo: row 에서 게시글 id 받아오기(row.id)
		const postId = row.id;
		// [이벤트 매핑] Todo: 게시글 상세 페이지(/post/:id) 로 이동
		navigate(`/my-icare/${postId}`);
	};
	return (
		<>
			<h2>나의 마일리지</h2>
			<Grid container direction='row' justifyContent='flex-end' spacing={2}>
				<Grid item>
					<Button variant='contained' onClick={rechargeClickOpen}>
						충전하기
					</Button>
				</Grid>
				<Grid item>
					<Button variant='contained' color='error' onClick={exchangeClickOpen}>
						환전하기
					</Button>
				</Grid>
			</Grid>
			<Grid container alignItems='center' spacing={1}>
				<Grid item>
					<Typography variant='h5'>보유 마일리지 : </Typography>
				</Grid>
				<Grid item>
					<TextField
						id='input-with-icon-textfield'
						InputProps={{
							startAdornment: (
								<InputAdornment position='start'>
									<LocalAtmIcon />
								</InputAdornment>
							),
							readOnly: true,
						}}
						variant='standard'
						value='10,000'
					/>
				</Grid>
			</Grid>
			<Grid congtainer style={{marginTop: 50}} spacing={1}>
				<Grid item>
					<Typography variant='h5'>마일리지 사용내역</Typography>
				</Grid>
				<Grid item>
					<DataTable columns={columns} rows={data} rowsPerPageOptions={[10, 20, 30]} isLoading={isLoading} />
				</Grid>
			</Grid>
			<RechargeDialog selectedValue={selectedValue} open={rechargeOpen} onClose={rechargeClose} />
			<ExchangeDialog selectedValue={selectedValue} open={exchangeOpen} onClose={exchangeClose} />
		</>
	);
};
export default Mileage;

// [이벤트 매핑] DataTable에 들어갈 columns 설정
const columns = [
	{
		id: 'date',
		label: '날짜',
		width: 5,
		align: 'left',
	},

	{
		id: 'type',
		label: '구분',
		width: 80,
		align: 'left',
	},
	{
		id: 'change',
		label: '마일리지 변경내용',
		width: 80,
		align: 'left',
	},
	{
		id: 'remain',
		label: '잔여 마일리지',
		width: 80,
		align: 'left',
	},
];
