import React from 'react';
import {Dialog, DialogTitle, DialogContent, Grid, Typography, Divider, TextField, Button, FormControl, FormLabel, RadioGroup, FormControlLabel, Radio, InputAdornment} from '@mui/material';
import LocalAtmIcon from '@mui/icons-material/LocalAtm';

function RechargeDialog(props) {
	const {onClose, selectedValue, open} = props;

	const handleClose = () => {
		onClose(selectedValue);
	};

	const [value, setValue] = React.useState(0);

	return (
		<Dialog onClose={handleClose} open={open}>
			{/* <Grid container direction='row' alignItems='center'>
				<Grid item>
					<Avatar alt='Natacha' src='/avatar4.jpeg' />
				</Grid>
				<Grid item>
					<Typography variant='h3'>{'전무룡'}님의 정보</Typography>
				</Grid>
			</Grid> */}
			<DialogTitle>
				<Typography variant='h4'>마일리지 충전하기</Typography>
			</DialogTitle>
			<Divider style={{margin: '0 15px 0 15px'}} />
			<DialogContent>
				<Grid container direction='column' spacing={3}>
					<Grid item>
						<FormControl>
							<FormLabel id='demo-radio-buttons-group-label'>충전 마일리지 선택</FormLabel>
							<RadioGroup aria-labelledby='demo-radio-buttons-group-label' defaultValue='2,000' name='radio-buttons-group'>
								<FormControlLabel value='2,000' control={<Radio />} label='2,000' />
								<FormControlLabel value='5,000' control={<Radio />} label='5,000' />
								<FormControlLabel value='10,000' control={<Radio />} label='10,000' />
							</RadioGroup>
						</FormControl>
					</Grid>
				</Grid>
				<Grid container direction='row' alignItems='center' spacing={2} mt={1}>
					<Grid item>
						<Typography variant='h5'>충전 후 잔액 : </Typography>
					</Grid>
					<Grid item>
						<Grid item>
							<TextField
								id='input-with-icon-textfield'
								InputProps={{
									startAdornment: (
										<InputAdornment position='start'>
											<LocalAtmIcon />
										</InputAdornment>
									),
									readOnly: true,
									sx: {width: 150},
								}}
								variant='standard'
								value='10,000'
							/>
						</Grid>
					</Grid>
				</Grid>
			</DialogContent>
			<Divider style={{margin: '0 15px 0 15px'}} />
			<Grid container justifyContent='flex-end'>
				<Button
					variant='contained'
					// [이벤트 매핑] Todo: 수정 버튼을 클릭하면 게시글 수정 페이지로 이동해야 합니다.
					// onClick={}
					style={{margin: '20px 20px 10px 0'}}>
					충전
				</Button>
			</Grid>
		</Dialog>
	);
}

export default RechargeDialog;
