import React from 'react';
import {Dialog, DialogTitle, DialogContent, Grid, Typography, Divider, TextField, Button, InputAdornment} from '@mui/material';
import LocalAtmIcon from '@mui/icons-material/LocalAtm';

function ExchangeDialog(props) {
	const {onClose, selectedValue, open} = props;

	const handleClose = () => {
		onClose(selectedValue);
	};

	const [value, setValue] = React.useState(0);

	return (
		<Dialog onClose={handleClose} open={open}>
			{/* <Grid container direction='row' alignItems='center'>
				<Grid item>
					<Avatar alt='Natacha' src='/avatar4.jpeg' />
				</Grid>
				<Grid item>
					<Typography variant='h3'>{'전무룡'}님의 정보</Typography>
				</Grid>
			</Grid> */}
			<DialogTitle>
				<Typography variant='h4'>마일리지 환전</Typography>
			</DialogTitle>
			<Divider style={{margin: '0 15px 0 15px'}} />
			<DialogContent>
				<Grid container direction='row' alignItems='center' spacing={2} mt={1}>
					<Grid item>
						<Typography variant='h5'>환전할 마일리지 : </Typography>
					</Grid>
					<Grid item>
						<Grid item>
							<TextField
								id='input-with-icon-textfield'
								InputProps={{
									startAdornment: (
										<InputAdornment position='start'>
											<LocalAtmIcon />
										</InputAdornment>
									),
									sx: {width: 150},
								}}
								variant='standard'
							/>
						</Grid>
					</Grid>
				</Grid>
				<Grid container direction='row' alignItems='center' spacing={2} mt={4}>
					<Grid item>
						<Typography variant='h5'>환전 후 잔액&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: </Typography>
					</Grid>
					<Grid item>
						<Grid item>
							<TextField
								id='input-with-icon-textfield'
								InputProps={{
									startAdornment: (
										<InputAdornment position='start'>
											<LocalAtmIcon />
										</InputAdornment>
									),
									readOnly: true,
									sx: {width: 150},
								}}
								variant='standard'
								value='10,000'
							/>
						</Grid>
					</Grid>
				</Grid>
			</DialogContent>
			<Divider style={{margin: '0 15px 0 15px'}} />
			<Grid container justifyContent='flex-end'>
				<Button
					variant='contained'
					// [이벤트 매핑] Todo: 수정 버튼을 클릭하면 게시글 수정 페이지로 이동해야 합니다.
					// onClick={}
					style={{margin: '20px 20px 10px 0'}}
					color='error'>
					환전
				</Button>
			</Grid>
		</Dialog>
	);
}

export default ExchangeDialog;
